.slick-prev,
.slick-next {
    font-size: 0;
    line-height: 0;

    position: absolute;
    top: 50%;

    display: block;

    width: 20px;
    height: 20px;
    padding: 0;
    transform: translate(0, -50%);

    cursor: pointer;

    color: transparent;
    border: none;
    outline: none;
    background: transparent;
    z-index: 9999;
}

.slick-prev::before {
    background-image: url(/static/media/slick_arrow.891cf2c4.svg) !important;
    left: 227px !important;
    position: absolute !important;
    content: '.' !important;
    color: transparent !important;
    width: 20px;
    background-repeat: no-repeat;
    background-size: contain;
}


.slick-next::before {
    background-image: url(/static/media/slick_arrow.891cf2c4.svg) !important;
    transform: rotate(180deg);
    right: 227px;
    position: absolute;
    content: '.' !important;
    color: transparent !important;
    width: 20px;
    background-repeat: no-repeat;
    background-size: contain;
